* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
	font-size: 100%;
	word-wrap: break-word;
	-ms-word-wrap: break-word;
}

*:hover {
	transition-property: background, color;
	transition-duration: .5s;
	transition-timing-function: ease-out;
}

#jacket,
#spine {
	border-top: 14px solid #f3f3f3;
}

html {
	background: #fff;
}

/****************************************************************************
	Spine adjustments
****************************************************************************/
.spine-actions,
.spine-footer {
	display: none;
}

#wsu-signature {
	background-image: none;
	pointer-events: none;
	background-size: contain;
}

#spine.cropped.skimmed #wsu-signature,
.print #wsu-signature,
.spine-mobile #wsu-signature {
	background-image: none;
	background-color: #3e3e3d;
}

#shelve, #shelve::after,
#shelve::before {
	border-color: #fff;
}

#spine .spine-sitenav a:hover,
#spine .spine-sitenav li.dogeared > a {
	color: #a03321;
}

#spine .spine-global-links,
#spine .spine-sitenav ul li {
	border-color: #f3f3f3;
}

@media screen and (max-width: 989px) {
	#binder #spine header {
		background-color: #3e3e3d;
	}
}

/****************************************************************************
	HTML adjustments
****************************************************************************/
main {
	background: #fff;
	color: #3e3e3d;
}

.column.pad-top {
	padding-top: 6rem;
}

hr {
	border: none;
	margin: 1em 0;
	height: 8px;
	background-color: #f3f3f3;
}

.featured-image {
	height: 300px;
	margin-bottom: 3em;
}

.article-header hgroup.source,
.article-header .hgroup.source {
	display: none;
}

/****************************************************************************
	Header adjustments
****************************************************************************/
.main-header {
	background-color: transparent;
	background-image: url('assets/NARA-logo.png');
	background-repeat: no-repeat;
	background-position: 20px;
	margin-bottom: 2em;
}

.main-header sup,
.main-header sub {
	display: none;
}

/****************************************************************************
	Typography adjustments
****************************************************************************/
a {
	color: #a03321;
	transition: color .3s ease-in-out;
}

a:hover {
	color: #cb3f28;
}

p,
main ol li,
main ul li {
	font-size: 1.1em;
	line-height: 1.6;
}

h2,
h4,
h5 {
	color: #268fca;
}

h1 {
	color: #3e3e3d;
	font-size: 2.4rem;
	font-weight: 300;
	margin-bottom: 1.4em;
	text-transform: uppercase;
	padding-bottom: .3em;
	border-bottom: 2px solid #f3f3f3;
}

h2 {
	font-size: 2.1rem;
	line-height: 1.2;
	padding-bottom: .4em;
}

h3 {
	color: #5e5f5f;
	padding-top: 1.2em;
	padding-bottom: 1em;
	font-weight: 600;
}

h5 {
	font-weight: 300;
}

.wsuwp-uc-people h3,
.column.two h4 {
	color: #5e5f5f;
	font-weight: 400;
	margin-bottom: 1.1em;
	padding-top: 1.5em;
	padding-bottom: .5em;
	border-bottom: 1px solid #5e5f5f;
}

blockquote {
	font-family: "Open Sans";
	font-weight: 700;
	font-size: 1.2rem;
	color: #6d6d6d;
}

.quotables blockquote {
	font-size: .9rem;
}

.labeled {
	background: #333;
	color: #fff;
	padding: .5em;
}

/****************************************************************************
	Homepage
****************************************************************************/
.builder-section-banner .column,
.spacing-loose .single.gutter.builder-section-banner .column:not(.gutterless) {
	padding: 0;
}

.builder-banner-inner-content {
	text-align: center;
}

.builder-banner-inner-content h1 {
	color: #fff;
	border-bottom: 1px solid #fff;
	padding-bottom: .2em;
	margin-bottom: 0;
}

/****************************************************************************
	Blog & archive
****************************************************************************/
footer.article-footer,
footer.main-footer {
	margin-left: 0;
}

h2.article-title {
	padding-bottom: .1em;
}

.article-header hgroup.source,
.article-header .hgroup.source {
	padding-bottom: 2em;
}

.single-post .article-title {
	margin-bottom: .5em;
}

/****************************************************************************
	Utilities
****************************************************************************/

/****************************************
	HTML Snippet 763-68
****************************************/
.five-teams .size-thumbnail {
	width: 120px;
	height: 120px;
}

.five-teams-label b,
.five-teams-label strong {
	font-size: 2.2rem;
	line-height: 1;
}

.five-teams-label {
	font-size: 1.1rem;
	color: #5e5f5f;
}

.five-teams-inside .size-thumbnail {
	width: 80px;
	height: 80px;
	margin-bottom: 0;
	margin-top: .8em;
}

.five-teams-inside h4 {
	font-size: 1.3em;
}

.five-teams-inside p {
	font-size: .9em;
}

.five-teams-inside p .full-size {
	font-size: 1.1em;
}

.five-teams-inside hr {
	height: 2px;
}

.home h2.five-teams-heading,
.home .five-teams .full-size {
	display: none;
}

/****************************************
	Teams
****************************************/
.teams {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	width: 100%;
	padding-left: 0;
	padding-right: 0;
}

.teams li {
	list-style: none;
	margin-bottom: 30px;
	min-width: 200px;
	flex: 1;
	margin-right: 50px;
}

.teams li:last-of-type {
	margin-right: 0;
}

/****************************************
	Orgs
****************************************/
.orgs {
	display: flex;
	flex-direction: column;
	flex-wrap: wrap;
	height: 500px;
}

.orgs li.list-heading {
	list-style: none;
	padding-top: 0;
	padding-bottom: 0;
}

.orgs li.list-heading h3 {
	padding-top: 0;
}

.single-wsuwp_uc_entity figure.article-thumbnail {
	margin-top: 5em;
	height: 150px;
	float: none;
}

.post-type-archive-wsuwp_uc_entity figure.article-thumbnail {
	float: none;
}

.post-type-archive-wsuwp_uc_entity .article-content {
	max-height: 205px;
	overflow: hidden;
}

.post-type-archive-wsuwp_uc_entity main section {
	border-bottom: 4px solid #f3f3f3;
}
.post-type-archive-wsuwp_uc_entity main section:first-of-type,
.post-type-archive-wsuwp_uc_entity main section:last-of-type {
	border-bottom: 0;
}

.post-type-archive-wsuwp_uc_entity .archive-footer {
	display: none;
}

.universities {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	width: 100%;
	padding-left: 0;
	padding-right: 0;
}

.universities li {
	list-style: none;
	margin-bottom: 30px;
	max-width: 150px;
	margin-right: 80px;
	text-align: center;
}

.universities li:last-of-type{
	margin-right: 0;
}

/****************************************
	People
****************************************/
.single-wsuwp_uc_person h1.article-title {
	width: inherit;
	margin-bottom: .5em;
}

article.wsuwp_uc_person {
	border-bottom: 4px solid #f3f3f3;
	min-height: 150px;
	padding-top: 1em;
	padding-bottom: 1em;
}

article.wsuwp_uc_person h3.article-title {
	padding-bottom: .4em;
}

.post-type-archive-wsuwp_uc_person .archive-footer {
	display: none;
}

.team-people {
	background: #efefef;
	padding: 1em;
	position: relative;
	float: left;
	width: 100%;
	display: block;
}

.team-people .team-person {
	border-bottom: 1px solid #fff;
	padding-bottom: 1em;
	padding-top: 1em;
	min-height: 80px;
	display: block;
}

.team-people .team-person:last-of-type {
	border-bottom: 0;
}

.team-people .team-person img {
	width: 80px;
}

.single-wsuwp_uc_project .main-footer {
	display: none;
}

/****************************************
	Buttons and forms
****************************************/
.button {
	background: #a03321;
	border: none;
	border-radius: 0;
	color: #fff;
	font-size: 1.2rem;
	font-weight: 300;
	letter-spacing: 1px;
}

.button:hover {
	border: none;
	color: #a03321;
}

/****************************************************************************
	Footer
****************************************************************************/
footer {
	font-family: 'open sans';
	font-weight: 300;
	border-top: 1px solid #f3f3f3;
	padding-top: 1.2em;
	padding-bottom: 1em;
	margin-left: 198px;
	margin-top: 2em;
}

footer .logos {
	float: left;
	margin-right: 20px;
}

footer .footer-logo{
	width: 65px;
	height: 65px;
	margin-left: 10px;
}

footer header {
	font-weight: 700;
	padding-bottom: .5em;
}

footer a .social-logo:nth-child(even) {
	margin: 0 20px;
}

/****************************************************************************
	Media Quries
****************************************************************************/
@media screen and (max-width: 990px) {
	.column.pad-top {
		padding-top: 0;
	}

	footer {
		padding: 2em;
		margin-left: 0;
	}

	.teams li {
		min-width: 400px;
		margin-right: 0;
	}
}

@media screen and (max-width: 693px) {
	.five-teams {
		display: flex;
		flex-direction: column;
	}

	.five-teams .one {
		order: 2;
		padding-top: 0;
	}

	.five-teams .two {
		padding-bottom: 0;
	}

	.five-teams-label b,
	.five-teams-label strong {
		color: #268fca;
	}

	.orgs {
		display: block;
		height: 100%;
	}
}
